.questions {
  background: #41bfef;
  background: url(%=static=%/content/questions-bg.png)no-repeat 50%;
  background-size: cover;
  margin-bottom: -40px;
  position: relative;
  z-index: 10;
}

.questions__wrap {
  padding: 20px;
  padding-top: 40px;
  padding-bottom: 60px;
}

.questions__ttl {
  font-size: 20px;
  color: #fff;
  font-family: $font-1-exb;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
}

.questions__txt {
  display: block;
  font-family: $font-1-sb;
  font-size: 16px;
  text-align: center;
  color: #fff;
}

.questions__txt-phone {
  display: block;
  font-family: $font-1-b;
  font-size: 21px;
  color: #fff;
  text-decoration: none;
  text-align: center;
  &:before {
    content: '';
    display: inline-block;
    @include bg($header-phone-icon);
    margin-right: 20px;
  }
}

.questions__or {
  display: block;
  font-family: $font-1-exb;
  text-align: center;
  color: #fff;
  font-size: 18px;    
}

.questions__name,
.questions__phone,
.questions__textarea {
  display: block;
  padding-left: 20px;
  width: 250px;
  color: #000;
  margin-left: auto;
  margin-right: auto;
}

.questions__textarea {
  resize: none;
  min-height: 120px;
}

.questions__submit {
  @include btn;  
  margin-top: 15px;
}


@media #{$tablet} {
  .questions__wrap {
    max-width: $max-tablet;
    margin: auto;
    @include flex-row;
    padding-top: 80px;
    justify-content: space-between;
  }
  
  
  
  .questions__ttl {
    font-size: 32px;
  }
  
  .questions__txt {
    font-size: 25px;
  }
  
  .questions__txt-phone {
    font-size: 32px;
    text-align: left;
    &:before {
      @include bg($phone-icon);
      margin-bottom: -5px;
      margin-right: 15px;
    }
  }
  
  .questions__or {
    font-size: 23px;
    text-align: left;
    margin-top: 10px;
  }
  
  .questions__arrow {
    @include bg($questions-arrow);
    margin-left: auto;
    margin-top: -70px;
  }
  
  .questions__name,
  .questions__phone {
    margin-bottom: 15px;
  }
    
}


@media #{$desktop} {
  .questions {
    background-size: cover;
    margin-bottom: -80px;
  }
  .questions__wrap {
    max-width: $max-desktop;
    padding-top: 50px;
    padding-bottom: 130px;
    padding-left: 90px;   
    margin-top: 45px;
    justify-content: flex-start;
  }
  
  .questions__ttl {
    font-size: 40px;
    padding-top: 120px;
  }
  
  .questions__txt {
    font-size: 32px;
  }
  
  .questions__txt-phone {
    font-size: 40px;
    margin-top: 15px;
    &:before {
      margin-right: 25px;
    }
  }
  
  .questions__or {
    font-size: 26px;
    margin-top: 30px;
  }
  
  .questions__arrow {
    margin-right: -110px;
    margin-top: -135px;
  }
  
  .questions__form {
    margin-left: 190px;
    margin-top: 75px;
  }
  
  .questions__name,
  .questions__phone {
    padding-top: 13px;
    padding-bottom: 13px;
    width: 335px;
    margin-bottom: 17px;
  }
  
  .questions__textarea {
    width: 335px;
    height: 150px;
  }
  
  .questions__submit {
    padding-top: 13px;
    padding-bottom: 13px;
    font-size: 18px;
    width: 370px;
  }
}