.how-work {
  
}

.how-work__wrap {
  padding: 20px;
}

.how-work__ttl {
  font-size: 18px;
  font-family: $font-1-exb;
  color: $color-1;
  text-align: center;
  text-transform: uppercase;
}

.how-work__items {
  @include nl;
  @include flex;
  align-items: center;
  justify-content: center;
}

.how-work__item {
  @include flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}

.how-work__img {
  height: 134px;
  width: 205px;
}

.how-work__txt {
  font-family: $font-1-sb;
  font-size: 13px;
  width: 200px;  
  color: #575757;
}


@media #{$tablet} {
  .how-work__wrap {
    max-width: $max-tablet;
    margin: auto;
    margin-top: 50px;
  }
  
  .how-work__ttl {
    font-size: $h2-t;
  }
  
  .how-work__items {
    flex-direction: row;
    align-items: flex-start;
  }
  
  .how-work__item {
    align-items: flex-start;
  }
  
  .how-work__item--2 {
    padding-top: 100px;
    margin-left: -29px;
  }
  
  .how-work__img {
    height: 100%;
    width: 100%;
  }
  
  .how-work__txt {
    font-size: 17px;
    width: auto;
    margin: 0;
    margin-top: 30px;
  }
  
  .how-work__txt--2 {
    text-align: right;
  }
  
  .how-work__arrow {
    @include bg($how-work-arrow);
    margin-left: auto;
    margin-right: 70px;
    margin-top: 10px;
  }
}


@media #{$desktop} {
  .how-work__wrap {
    max-width: $max-desktop;
    margin-top: 170px;
  }
  
  .how-work__ttl {
    font-size: $h2-d;
  }
  
  .how-work__items {
    margin-top: 35px;
  }
  
  .how-work__item--2 {
    margin-top: 20px;
  }
  
  .how-work__txt {
    font-size: 18px;
    line-height: 26px;
  }
  
  .how-work__txt--2 {
    display: block;
    padding-left: 25px;
  }
  
  .how-work__arrow {
    margin-right: 90px;
    margin-top: 25px;
  }
}