.reviews__wrap {
  padding: 20px;
  padding-top: 0;
}

.reviews__ttl {
  font-size: $h2-m;
  font-family: $font-1-exb;
  color: #464646;
  text-transform: uppercase;
  text-align: center;
}

.reviews__items {
  @include nl;  
  @include flex;
  padding-top: 10px;
  padding-bottom: 10px;  
  min-width: 280px;
  box-shadow: 0px 0px 10px 1px rgba(0,0,0, 0.1);  
}

.reviews__item {  
  margin-right: 40px;
  @include flex;
  justify-content: center;
  align-items: center;  
  margin: auto;
  margin-top: 20px;
  margin-bottom: 20px;
  padding-top: 20px;  
}

.reviews__img-block {
  height: 101px;
  width: 101px;
  border: 50%;
  overflow: hidden;
}

.reviews__img {
  height: 101px;
  width: 101px;
}

.reviews__name {
  font-family: $font-1-sb;
  font-size: 18px;
  text-align: center;
  margin: 0;
  margin-top: 20px;
}

.reviews__city {
  font-size: 16px;
  text-align: center;
  margin-top: 5px;
}

.reviews__vk-link {
  color: #3dbeef;
  font-family: $font-1-sb;
  margin-top: 10px;
}

.reviews__txt {
  font-family: $font-1-l-italic;
  font-size: 16px;
  line-height: 24px;
  padding-left: 20px;
  padding-right: 20px;
  text-align: center;
  width: 280px;
}


@media #{$tablet} {
  .reviews__wrap {
    max-width: $max-tablet;
    margin: auto;
    margin-top: 40px;
  }
  
  .reviews__ttl {
    font-size: $h2-t;
  }
  
  .reviews__items {
    box-shadow: none;
    overflow: hidden;
  }
  
  .reviews__item {
    box-shadow: 0px 0px 10px 0px rgba(0,0,0, 0.1);
    margin-right: 10px;
    margin-left: 10px;   
    width: 222px;
  }
  
  .reviews__txt {
    width: 200px;
  }
  
  .reviews__name {
    width: 200px;
  }
}


@media #{$desktop} {
  .reviews__wrap {
    max-width: $max-desktop;
    margin-top: 10px;  
    padding: 0;    
  }
  
  .reviews__ttl {    
    font-size: $h2-d;
    margin: 0;    
  }
  
  .reviews__items-wrap {
    @include flex-row;  
    align-items: center;
  }
  
  .reviews__items {
    margin: 0;
    margin-top: 27px;   
    padding-left: 70px;       
    width: 1070px;
  } 
  
  
  .reviews__item {
    position: relative;
    width: 305px;
    margin-right: 20px;    
    &:nth-of-type(3n) {
      margin-right: 0;
    }
  }
  
  .reviews__prev {
    cursor: pointer;
    @include bg($reviews-prev);
  }
  
  .reviews__next {
    cursor: pointer;
    @include bg($reviews-next);
  }
}