.save {
  background: #5bd4ff;
  box-shadow: inset 0px 0px 100px 0px #1f99d4;
  &:after {
    display: block;
    content: '';
    background: #fff;
    width: 100%;
    height: 50px;
    transform: skewY(-2deg);
    margin-bottom: -20px;
  }
}

.save__wrap {
  padding: 20px;  
}

.save__ttl {
  font-family: $font-1-exb;
  font-size: 17px;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  margin: 0;
}

.save__ttl-sm {
  font-family: $font-1;
  display: inline-block;
  margin-right: 5px;
}

.save__ttl-b {  
  font-family: $font-1-b;
}

.save__items {
  @include nl;
  margin: auto;
  margin-top: 20px;
  width: 280px;  
}

.save__item {
  @include flex;
  justify-content: center;
  align-items: center;  
}

.save__item-ttl {
  font-family: $font-1-b;
  font-size: 18px;
  color: #fff;
  text-transform: uppercase;
  margin-top: 15px;
}

.save__txt {
  font-family: $font-1-italic;
  font-size: 16px;
  line-height: 23px;
  color: #fff;
  text-align: center;
  margin: 0px;
  margin-top: 5px;
  width: 200px;
}

.save__ear {  
  @include bg($ear);
}

.save__micro {
  @include bg($micro);
}

.save__dynamic {
  @include bg($dynamic);
}

.save__indicators {
  @include flex-row;
  margin: auto;
  justify-content: center;
  & .active:after {
    background: $color-2;
  }
}

.save__indicator {
  @include dot;
}


@media #{$tablet} {
  .save {
    box-shadow: inset 0px 0px 500px 0px #1f99d4;
    &:after{
      margin-bottom: -25px;
    }
  }
  .save__wrap {
    max-width: $max-tablet;
    margin: auto;
    padding-top: 50px;
    padding-bottom: 50px;
  }
  
  .save__ttl {
    font-size: $h1-t;
  }
  
  .save__ttl-sm {
    font-size: 22px;
  }
  
  .save__ttl-b {
    font-size: 22px;
  }
  
  .save__items {
    @include flex-row;
    width: auto;
    margin-top: 50px;
  }
  
  .save__item {
    width: 33.3%;
    justify-content: flex-start;
  }
  
  .save__indicators {
    display: none !important;
  }
  
  .save__item-ttl {
    font-size: 16px;
  }
  
  .save__txt {
    font-size: 16px;
    width: 200px;
  }
}


@media #{$desktop} {
  .save {
    margin-top: -100px;
    box-shadow: none;
    background: #1f9bd4; 
    background: url(%=static=%/content/save-bg.png)no-repeat 50% 0%;
    &:after {
      height: 90px;
      margin-bottom: -50px;
    }
  }
  
  .save__wrap {
    max-width: $max-desktop;
    padding-top: 160px;  
    padding-bottom: 60px;
  }
  
  .save__ttl {
    text-align: right;
    font-size: $h1-d;
    letter-spacing: -0.5px;
  }
  
  .save__ttl-sm {
    display: inline-block;
    font-size: 33px;    
  }
  
  .save__ttl-b {    
    display: inline-block;
    margin-top: -10px;
    font-size: 33px;
  }
  
  .save__items {
    width: 720px;
    margin: 0;
    margin-top: 90px;
    margin-left: auto;
  }
  
  .save__item {
    width: 200px;
    margin-right: 60px;
    &:nth-of-type(3n) {
      margin-right: 0;
    }
  }
  
  .save__item-ttl {
    font-size: 18px;
    margin-top: 30px;
  }
  
}
