.callback {
  background: #fcfcfc;
}

.callback__wrap {
  padding: 20px;
  padding-top: 30px;
} 

.callback__ttl {
  font-size: 18px;
  font-family: $font-1-exb;
  color: $color-1;
  text-align: center;
  margin: 0;
  text-transform: uppercase;
}

.callback__ttl-b {
  font-size: 16px;
  color: $color-3;
  font-family: $font-1-sb;
  display: inline-block;
}

.callback__items {
  @include nl;
  @include flex;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
}

.callback__input {
  @include input;
  border-color: #e2e2e2;
  background: #fff;
  width: 225px;
  &:hover {
    border-color: $color-2;
  }
  &:focus {
    border-color: $color-2;
  }
}

.callback__submit {  
  @include btn;
  width: 280px;
  margin-top: 30px;
}

.form-ty { 
  position: fixed;  
  background: rgba(0,0,0, 0.3);
  height: 100%;  
  width: 100%;
  top: 0;
  z-index: 1000;  
  display: none;
}

.form-ty__wrap {   
  background: #fff;
  min-height: 161px;  
  border-radius: 1px;  
  min-width: 280px;
}

.form-ty__close {
  position: relative;
  @include bg($close-form);
  margin-left: auto;
  margin-right: -10px;
  top: -10px;
  cursor: pointer;
}

.form-ty__open {
  @include flex;
  justify-content: center;
  align-items: center;
}

.form-ty__ttl {
  font-family: $font-2-b;
  font-size: 20px;
  color: $color-1;  
  margin: 0;
  text-align: center;
  margin-top: 20px;
  text-transform: uppercase;
}

.form-ty__txt {
  font-family: $font-2;
  font-size: 15px;
  text-align: center;
}


@media #{$tablet} {
  .callback__wrap {
    max-width: $max-tablet;
    margin: auto;
    padding-top: 50px;
  }
  
  .callback__ttl {
    font-size: $h2-t;
  }
  
  .callback__ttl-b {
    font-size: 18px;
  }
  
  .callback__items {
    flex-direction: row;
    justify-content: space-between;
  }
  
  .callback__input {
    margin: 0;
    width: 180px;
  }
  
  .callback__submit {
    width: 200px;
    margin: 0;
    padding-top: 9px;
    padding-bottom: 9px;
    margin-top: -4px;
  }
}


@media #{$desktop} {
  .callback__wrap {
    max-width: $max-desktop;
    padding-top: 55px;
  }
  
  .callback__ttl {
    font-size: $h2-d;
  }
  
  .callback__ttl-b {
    font-size: 22px;
  }
  
  .callback__input {
    width: 320px;
    padding-top: 12px;
    padding-bottom: 12px;
  }
  
  .callback__items {
    margin-top: 30px;
  }
  
  .callback__submit {
    width: 370px;
  }
}


