.footer {
  background: #4e4e4e;
}
.footer__wrap {
  padding: 20px;
  padding-top: 40px;
  padding-bottom: 40px;
}

.footer__logo-block {
  @include flex-row;
  justify-content: center;
  align-items: center;
}

.footer__logo-txt {
  font-family: $font-1-italic;
  font-size: 14px;
  margin-left: 20px;
  color: #fff;
}

.footer__txt {
  display: block;
  font-family: $font-1-sb;
  font-size: 16px;
  color: #fff;
  padding-left: 40px;
  margin: auto;
  width: 240px;
}

.footer__phone {
  display: block;
  font-family: $font-1-sb;
  font-size: 20px;
  color: #fff;
  text-decoration: none;
  margin: auto;
  margin-top: 20px;
  text-align: center;
  &:before {
    content: '';
    display: inline-block;
    @include bg($header-phone-icon);
    margin-right: 20px;
  }
}

.footer__link {
  @include btn;
  margin: auto;
  margin-top: 10px;  
}


@media #{$tablet} {
  .footer__wrap {
    max-width: $max-tablet;
    margin: auto;
    @include flex-row;
    align-items: center;
    justify-content: space-between;
    padding-top: 70px;
    padding-bottom: 50px;
  }
  
  .footer__txt {
    font-size: 18px;
  }
  
  .footer__phone {
    margin: 0;
  }
  
  .footer__link {
    width: 202px;
    padding-bottom: 7px;
    padding-top: 7px;
  }
  
}


@media #{$desktop} {
  .footer__wrap {
    max-width: $max-desktop;
    padding-top: 120px;
  }
  
  .footer__logo-txt {
    font-size: 16px;
  }
}