.advgs {
  background: #33aedd;  
  &:before {
    display: block;
    position: relative;
    content: '';
    background: #fff;
    width: 100%;
    height: 50px;
    transform: skewY(-2deg);
    top: -20px;
  }  
  &:after {
    position: relative;
    display: block;
    content: '';
    background: #fff;
    width: 100%;
    height: 50px;
    transform: skewY(2deg);
    bottom: -20px;
  }
}

.advgs__wrap {
  padding: 20px;  
}

.advgs__ttl {
  font-family: $font-1-exb;
  font-size: 18px;
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  margin: 0;
}

.advgs__items {
  @include nl;
  @include flex-row;
  margin-top: 20px;
}

.advgs__item {
  @include flex;
  justify-content: center;
  align-items: center;
}

.advgs__item-ttl {
  font-family: $font-1-b;
  font-size: 18px;
  color: #fff;
  text-transform: uppercase;
  margin-top: 15px;
}

.advgs__txt {
  font-family: $font-1-italic;
  font-size: 16px;
  line-height: 23px;
  color: #fff;
  text-align: center;
  margin: 0px;
  margin-top: 5px;
}

.advgs__free {  
  @include bg($free);
}

.advgs__safely {
  @include bg($safely);
}

.advgs__guarantee {
  @include bg($guarantee);
}

.advg__indicators {
  @include flex-row;
  margin: auto;
  justify-content: center;
  margin-top: 5px;
  & .active:after {
    background: $color-2;
  }
}

.advgs__indicator {
  @include dot;
}


@media #{$tablet} {
  .advgs {
    margin-top: 30px;
    &:before {
      top: -25px;
    }
    &:after {
      bottom: -25px;
    }
    
  }
  .advgs__wrap {
    max-width: $max-tablet;
    margin: auto;   
    margin-top: 20px;
  }
  
  .advgs__ttl {
    font-size: $h2-t;
  }
  
  .advgs__items {
    align-items: flex-start;
    margin-top: 50px;
    justify-content: space-between;
  }
  
  .advgs__item-ttl {
    font-size: 16px;
    margin-top: 25px;
  }
  
  .advgs__txt {    
    width: 200px;
    margin-top: 15px;
  }
  
  .advg__indicators {
    display: none !important;
  }
}


@media #{$desktop} {
  .advgs {
    background: #33aedd;
    background: url(%=static=%/content/advgs-bg.png)no-repeat 50%;
    margin-top: 30px;
    &:before {
      top: -40px;
      height: 90px;
    }
    &:after {
      bottom: -40px;
      height: 90px;
    }
  }
  
  .advgs__wrap {
    max-width: $max-desktop;   
    padding-bottom: 45px;
  }
  
  .advgs__ttl {
    font-size: $h2-d;
  }
  
  .advgs__items {
    margin: auto;
    margin-top: 60px;  
    justify-content: flex-start;
    width: 1100px;    
  }  
  
  .advgs__item {
    width: 250px;
    margin-right: 175px;
    &:nth-of-type(3n) {
      margin-right: 0;
    }
  }
  
  .advgs__item-ttl {
    font-size: 18px;
  }
  
  .advgs__txt {
    width: auto;
  }
}