/* Your custom fonts here */
@include font-face('Open Sans', '../fonts/Open Sans', normal, italic, $exts: woff woff2 ttf);
@include font-face('Open Sans Bold', '../fonts/Open Sans Bold', normal, italic, $exts: woff woff2 ttf);
@include font-face('RobotoBold', '../fonts/Roboto Bold V2', normal, italic, $exts: woff woff2 ttf);
@include font-face('Open Sans Light Italic', '../fonts/Open Sans Light Italic', normal, italic, $exts: woff woff2 ttf);
@include font-face('Open Sans Italic', '../fonts/Open Sans Italic', normal, italic, $exts: woff woff2 ttf);
@include font-face('Open Sans Semibold', '../fonts/Open Sans Semibold', normal, italic, $exts: woff woff2 ttf);
@include font-face('Open Sans Extrabold', '../fonts/Open Sans Extrabold', normal, italic, $exts: woff woff2 ttf);

@include font-face('Roboto Regular V2', '../fonts/Roboto Regular V2', normal, italic, $exts: woff woff2 ttf);
@include font-face('Roboto Bold V2', '../fonts/Roboto Bold V2', normal, italic, $exts: woff woff2 ttf);

@include font-face('Roboto Light V2', '../fonts/Roboto Light V2', normal, italic, $exts: woff woff2 ttf);
@include font-face('Roboto Medium V1', '../fonts/Roboto Medium V1', normal, italic, $exts: woff woff2 ttf);


$font-1: Open Sans, Helvetica, Arial, sans-serif;
$font-1-sb: Open Sans Semibold, Helvetica, Arial, sans-serif;
$font-1-b: Open Sans Bold , Helvetica, Arial, sans-serif;
$font-1-exb: Open Sans Extrabold, Helvetica, Arial, sans-serif;
$font-1-italic: Open Sans Italic, Helvetica, Arial, sans-serif;
$font-1-l-italic: Open Sans Light Italic, Helvetica, Arial, sans-serif;

$font-2: Roboto Regular V2, Helvetica, Arial, sans-serif;
$font-2-: Roboto Regular V2, Helvetica, Arial, sans-serif;
$font-2-l: Roboto Light V2, Helvetica, Arial, sans-serif;
$font-2-m: Roboto Medium V1, Helvetica, Arial, sans-serif;
$font-2-b: UbuntuBold, Helvetica, Arial, sans-serif;

$h1-m: 30px;
$h2-m: 18px;
$h3-m: 20px;
$h4-m: 18px;
$h5-m: 14px;

$h1-t: 32px;
$h2-t: 24px;
$h3-t: 24px;
$h4-t: 22px;
$h5-t: 20px;

$h1-d: 47px;
$h2-d: 28px;
$h3-d: 28px;
$h4-d: 20px;
$h5-d: 20px;




