.header {
  background: $color-2;
  border-bottom: 1px solid #5dd3ff;
  box-shadow: 0px 1px 0px 0px #1c84b9;
}

.header__wrap {
  padding: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
}

.header__logo-block {
  @include flex-row; 
}

.header__logo {
  height: 50px;
  width: 50px;
  margin-right: 10px;
}

.header__logo-txt {
  font-family: $font-1-italic;
  color: #fff;  
  font-size: 13px;  
}

.header__menu {
  @include nl;  
  margin-top: 20px;
  border-top: 1px solid #5dd3ff;
  box-shadow: 0px -1px 0px 0px #1c84b9;
  display: none;
}

.header__menu-link {
  display: block;
  text-decoration: none;
  color: #fff;
  font-size: 16px;
  text-align: center;
  padding: 11px;
  border-bottom: 1px solid #5dd3ff;
  box-shadow: 0px 1px 0px 0px #1c84b9;
  transition: 0.3s ease;  
  &:hover {
    transition: 0.3s ease;
    color: $color-1;
  }
}

.header__callback {
  display: none;
  margin-top: 20px;
}

.header__menu-open {
  display: block;
}

.header__cb-nbr {
  display: block;
  text-align: center;
  color: #fff;
  font-family: $font-1-sb;
  font-size: 20px;
  text-decoration: none;
  &:before {
    content: '';
    display: inline-block;
    @include bg($header-phone-icon);
    margin-right: 10px;
  }
}

.header__cb-btn {
  @include btn;
  margin-top: 10px;  
}

.header__form  {
  display: none;
}

.header__form-open {
  @include flex;
  justify-content: center;
  align-items: center;
}

/**forms**/

.form-cb__form-wrap {    
  background: #fff;  
  margin: auto;    
  width: 280px;  
}

.form-cb__close-btn {
  position: relative;
  @include bg($close-form);
  cursor: pointer;
  margin-left: auto;
  margin-right: -10px;  
  margin-top: -10px;
}

.form-cb__wrap {  
  padding: 20px;
  padding-top: 0;
}

.form-cb__ttl {
  font-family: $font-1-b;
  color: $color-1;
  font-size: 20px;
  text-transform: uppercase;
  text-align: center;
  font-weight: normal;  
  margin: 0;
}

.form-cb__txt {
  display: block;
  font-size: 15px;
  color: $color-1;
  text-align: center;
}

.form-cb__input {
  border: 1px solid #f8f8f8;
  border-width: 1px;
  border-radius: 7px;
  background: #f8f8f8;
  font-family: $font-2-l;
  color: $color-1;
  font-size: 16px;
  padding: 11px;  
  padding-left: 40px;  
  width: 190px;
  margin-top: 15px;
  &:hover {
    background: #fff;
    border: 1px solid $color-4;
  }
  &:focus {
    border-color: $color-4;
  }
}

.form-cb__submit {
  @include btn;
  margin-top: 20px;
}

.form-cb__model {
  display: block;
  text-align: center;
  font-size: 15px;
  font-family: $font-2-m;
}

.form-cb__info {
  margin-top: 20px;
  display: block;
  font-family: $font-2-l;
  font-size: 12px;
  line-height: 20px;
  color: #838383;
  text-align: center;
}

.form-cb__form {
  position: fixed;
  background: rgba(0,0,0, 0.3);
  height: 100%;  
  width: 100%;
  top: 0; 
  left: 0;
  z-index: 100;  
}


@media #{$tablet} {
  .header {
    background: #5bd4ff;
    box-shadow: inset 0px 0px 300px 0px #1f99d4;
    border: none;
  }
  
  .header__wrap {
    max-width: $max-tablet;
    margin: auto;
    @include flex-row;    
    justify-content: space-between;
    align-items: center;
  }
  
  .header__menu-btn {
    display: none;
  }
  
  .header__logo-txt {
    font-size: 12px;
  }
  
  .header__menu {
    @include flex-row;
    flex-wrap: wrap;
    border-top: none;
    box-shadow: none;
    margin: 0; 
    padding-left: 5px;
  }
  
  .header__menu-link {
    font-size: 13px;
    border-bottom: none;
    box-shadow: none;
    border-bottom: 2px;
  }
  
  .header__menu-link--active {
    border-bottom: 2px solid $color-4;    
  }
  
  .header__callback {
    margin: 0;
  }    

  .header__cb-btn {
    min-width: 0;
    max-width: none;
    width: 140px;
    padding: 4px;
  }
  
  .header__cb-nbr {
    font-size: 15px;
  }
}


@media #{$desktop} {
  .header {
    box-shadow: none;
    background: rgba(0,0,0,0.1);      
    z-index: 100;
    position: relative;
  }
  
  .header__wrap {
    max-width: $max-desktop;
    padding: 0;
    padding-left: 30px;
    padding-right: 30px;    
    justify-content: flex-start;
    align-items: flex-start;
  }
  
  .header__logo-block {
    margin-top: 15px;
  }
  
  .header__logo {
    height: 68px;
    width: 68px;
  }
  
  .header__logo-txt {
    font-size: 16px;
    line-height: 20px;
  }
  
  .header__menu {
    margin-left: 100px;
  }
  
  .header__menu-link {
    padding-top: 35px;
    padding-bottom: 35px;
    padding-right: 16px;
    padding-left: 16px;
    border-bottom: 7px;
    font-size: 16px;
    margin-right: 10px;
  }
  
  .header__menu-link--active {
    background: rgba(0,0,0, 0.1);
    border-bottom: 7px solid $color-4;
  }
  
  .header__callback {
    margin-left: auto;
  }
  
  .header__cb-nbr {
    font-size: 20px;   
    line-height: 34px;
    letter-spacing: 0.5px;
    margin-top: 5px;
    &:before {
      margin-right: 15px;
    }
  }
  
  .header__cb-btn {
    padding-top: 9px;
    padding-bottom: 9px;
    width: 190px; 
    margin-top: 3px;
  }
}
