.catalog {}

.catalog__wrap {
  padding: 20px;
  padding-top: 0;
  padding-bottom: 0;
}

.catalog__ttl {
  font-family: $font-1-exb;
  font-size: 18px;
  text-transform: uppercase;
  text-align: center;
}

.catalog__items {
  @include nl;
  @include flex-row;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 40px;
}

.catalog__item {
  @include flex;
  align-items: center;
  margin-bottom: 40px;
  width: 280px;
  margin-right: 10px;  
  margin-left: 10px;
}

.catalog__form {
  display: none;
  justify-content: center;
  align-items: center;
}

.catalog__item-img {
  box-shadow: 0px 0px 20px 1px rgba(0,0,0, 0.1);
  width: 280px;  
}

.catalog__item-img-top {
  @include flex-row;
  justify-content: space-between;
}

.catalog__discount { 
  position: relative;
  background: #ffda22;
  box-shadow: inset 0px 0px 50px 0px #ffbd18;
  width: 70px;
  height: 70px; 
  
  &:after {
    content: '';
    position: absolute;
    display: block;
    width: 0;
    height: 0;
    top: 0px;    
    right: 0;
    left: 0;
    bottom: 0;
    margin-top: 0;
    border-bottom: 71px solid #fff;
    border-left: 71px solid transparent;    
  }
}

.catalog__dst-value {
  display: inline-block;
  transform: rotate(-45deg);
  font-family: $font-1-b;
  color: #fff;
  font-size: 15px;
  max-width: 70px;
  margin-top: 12px;
  margin-left: 2px;  
}

.catalog__item-img-info {
  @include flex-row;
  justify-content: center;
  padding-top: 20px;
  padding-right: 15px;
}

.catalog__item-like {
  @include bg($like);
  margin-right: 20px;
}

.catalog__item-img-txt {
  font-size: 12px;
  color: #a1a1a1;
  margin: 0;
}

.catalog__img {
  display: block;
  width: 254px;
  height: 88px;
  margin: auto;  
}

.catalog__info {
  width: 280px;
}

.catalog__info-ttl {
  font-family: $font-1-exb;
  text-transform: uppercase; 
  font-size: 14px;
  color: $color-1;
  margin-bottom: 0;
}

.catalog__info-for {
  font-family: $font-1-b;
  font-size: 14px;  
  color: $color-1;
  margin: 0;
  margin-top: 5px;
}

.catalog__info-txt {
  font-family: $font-1-italic;
  color: $color-3;
  font-size: 13px;
  margin: 0;
  margin-top: 5px;
}

.catalog__old-price {
  font-family: $font-1-sb;
  font-size: 17px;
  text-decoration: line-through;
  margin-right: 20px;
}

.catalog__new-price {
  font-family: $font-1-b;
  font-size: 25px;
  color: #ffc80a;
}

.catalog__link {
  @include btn;
  margin-top: 20px;
}


@media #{$tablet} {
  .catalog__wrap {
    max-width: $max-tablet;
    margin: auto;
  }
  
  .catalog__ttl {
    font-size: $h2-t;
  }
  
  .catalog__items {
    flex-direction: column;
    width: auto;
  }
  
  .catalog__item {
    @include flex-row;    
    width: auto;
    align-items: flex-start;
  }
  
  .catalog__discount {
    height: 110px;
    width: 110px;
    &:after {
      border-left-width: 111px;
      border-bottom-width: 111px;
      left: 0;
    }
  }
  
  .catalog__dst-value {
    margin-top: 17px;
    margin-left: 5px;
    font-size: 22px;
  }
  
  .catalog__item-img-txt {
    font-size: 14px;
  }
  
  .catalog__item-img {
    width: 45%;
    margin-right: 20px;
    height: 100%;
  }  
  
  .catalog__img {
    height: 100%;
    width: 100%;
  }
  
  .catalog__img-block {
    height: auto;
    width: auto;
    padding: 15px;
    margin-top: 50px;    
  }    
  
  .catalog__info {
    width: 55%;
  } 
  
  .catalog__info-ttl {
    font-size: 20px;
  }
  
  .catalog__for {
    font-size: 16px;
  }
  
  .catalog__info-txt {
    font-size: 16px;
  }
  
}


@media #{$desktop} {
  .catalog__wrap {
    max-width: $max-desktop;
    padding-top: 20px; 
  }
  
  .catalog__ttl {
    font-size: $h2-d;
  }
  
  .catalog__items {
    margin-top: 60px;
    padding-left: 40px;
  }
  
  .catalog__item {
    margin-bottom: 85px;
  }
  
  .catalog__item-img {
    width: 475px;
    height: auto;
  }
  
  .catalog__discount {
    height: 140px;
    width: 140px;
    &:after {
      border-left-width: 141px;
      border-bottom-width: 141px;      
    }
  }
  
  .catalog__dst-value {
    font-size: 33px;
    margin-left: 0px;
    margin-top: 25px;
  }
  
  .catalog__item-img {
    margin-right: 40px;
    padding-bottom: 12px;
  }
  
  .catalog__item-img-info {
    padding-top: 16px;
  }
  
  .catalog__item-like {
    margin-top: 9px;
    margin-right: 15px;
  }
  
  .catalog__item-img-txt {    
    margin-right: 23px;
    line-height: 25px;
  }
  
  .catalog__img-block {
    margin: 0;
    padding-top: 0; 
  }
  
  .catalog__img {
    height: auto;
    width: auto;
  }
  
  .catalog__info {
    width: 565px;
  }
  
  .catalog__info-ttl {
    font-size: 20px;
    margin: 0;
    margin-top: 8px;
  }
  
  .catalog__info-for {
    font-size: 16px;
    margin-top: 20px;
  }
  
  .catalog__info-txt {
    font-size: 16px;
    line-height: 26px;
    margin-top: 5px;
    margin-bottom: 0;
  }
  
  .catalog__price {
    @include flex-row;
    margin-top: 60px;
  }
  
  .catalog__old-price {
    margin-right: 40px;
    line-height: 32px;
  }  
  
  .catalog__link {
    margin: 0;
    margin-left: 30px;
    width: 298px;
  }
}
